<template>
    <div class="undiagnosed">
      <el-row>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }">PACS</el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </el-row>

      <el-row class="operationNew">
        <el-col v-bind:class="{ active: isActive1 }" :span="2"><a @click="turnBlue" id="isActive1" href="javascript:;"></a></el-col>
        <el-col v-bind:class="{ active: isActive4 }" :span="3" class="timepicker"><a @click="" id="isActive4" href="javascript:;"><el-date-picker
          v-model="dateValue"
          align="right"
          type="date"
          @change="selectDate"
          placeholder=""
          :picker-options="pickerOptions">
        </el-date-picker></a></el-col>
        <el-col :span="8">&nbsp;</el-col>
        <el-col :span="2"><el-button type="primary" @click="handleAdd"><i class="el-icon-upload el-icon--right"></i></el-button></el-col>
        <div style="text-align: center">
        <el-col :span="2" ><a href="javascript:;" v-on:click="refresh"><i class="icon iconfont icon-shuaxin1"></i></a></el-col>
        <el-col :span="5"><el-input
          v-model="inputVal"
          placeholder="// ">
        </el-input></el-col>
        <el-col :span="2"><el-button type="primary" @click="searchBtn" icon="el-icon-search"></el-button></el-col>
        </div>
      </el-row>

      <el-row class="dataZone">
        <template>
          <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column
              prop="patient_id"
              label=""
              width="200">
            </el-table-column>
            <el-table-column
              prop="patient_name"
              label=""
              width="200">
            </el-table-column>
            <el-table-column
              prop="patient_gender"
              label=""
              width="200"
              :formatter="formatSex">
            </el-table-column>
            <el-table-column
              prop="patient_age"
              label=""
              width="200">
            </el-table-column>
            <el-table-column
              prop="patient_tel"
              label=""
              width="200">
            </el-table-column>
            <el-table-column
              prop="apply_time"
              label=""
              width="200">
            </el-table-column>
            <el-table-column
              prop="status"
              label=""
              :formatter="formatStatus">
            </el-table-column>
            <el-table-column
              fixed="right"
              label="">
              <template slot-scope="scope">
                <el-button  @click="clickReport(scope.$index, scope.row)" type="primary" size="small" v-if="formatButton(scope.row)" icon="el-icon-first-aid-kit" circle></el-button>
                <el-button @click="handleDel(scope.$index, scope.row)" type="danger" size="small" icon="el-icon-delete" circle v-if="!formatButton(scope.row)"></el-button>
                <el-button @click="handleEdit(scope.$index, scope.row)" type="primary" size="small" icon="el-icon-edit" circle></el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </el-row>
     <el-row class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5,10]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalNum">
      </el-pagination>
     </el-row>
      <!---->
      <el-dialog title="" :visible.sync="addFormVisible" v-model="addFormVisible" :close-on-click-modal="false">
        <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
          <el-form-item label=""  prop="patient_name" style="width: 25%">
            <el-input v-model="addForm.patient_name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="patient_tel">
            <el-input type="text" placeholder="" @blur="checkIsDiagnoseAgain" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" show-word-limit v-model="addForm.patient_tel" style="width: 25%"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="addForm.patient_gender">
              <el-radio class="radio" :label="1"></el-radio>
              <el-radio class="radio" :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-input-number v-model="addForm.patient_age" oninput="value=value.replace(/[^\d]/g,'')" :min="0" :max="200" style="width: 25%"></el-input-number>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="addForm.address"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click.native="addFormVisible = false"></el-button>
          <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
        </div>
      </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="editFormVisible" v-model="editFormVisible" :close-on-click-modal="false">
          <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
            <el-form-item label="" prop="patient_name" style="width: 25%">
              <el-input v-model="editForm.patient_name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="patient_tel">
              <el-input type="text" placeholder="" oninput="value=value.replace(/[^\d]/g,'')" maxlength="11" show-word-limit v-model="editForm.patient_tel" style="width: 25%"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="editForm.patient_gender">
                <el-radio class="radio" :label="'1'"></el-radio>
                <el-radio class="radio" :label="'0'"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="">
              <el-input-number v-model="editForm.patient_age" oninput="value=value.replace(/[^\d]/g,'')" :min="0" :max="200" style="width: 25%"></el-input-number>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" v-model="editForm.address"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false"></el-button>
            <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
          </div>
        </el-dialog>
    </div>
</template>

<script>
  import store from '@/vuex/store'
  import axios from 'axios'
  import {removePatient, checkPatient, editPatientBaicInfo, addPatient } from '../../api/api';
  import cookie from "../../static/js/cookie";
  let host = 'http://127.0.0.1:8000/';
  export default {
    name:'undiagnosed',
    created:function(){
      let page_info = this.$store.state.page_info;
      if(page_info.page_size&&page_info.currentPage){
        console.log('111122',page_info.page_size,page_info.currentPage);
        this.page_size = page_info.page_size;
        this.currentPage = page_info.currentPage;
        this.get_patients(page_info.currentPage,page_info.page_size);
      }else{
        console.log('222211',cookie.getCookie('hospital_id'));
        this.get_patients(1,10);
      }
    },
    methods:{
      /**/
      refresh:function () {
        let p = 1;
        this.currentPage = 1;
        let page_size = this.page_size;
        let search  = this.inputVal;
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("total");
        }
      },
      /*store.jspagesize*/
      delieverPageAndSize:function(){
        let currentPage = this.currentPage;
        let page_size = this.page_size;
        let page_info = {};
        page_info['currentPage'] = currentPage;
        page_info['page_size'] = page_size;
        this.$store.commit('rememberPageAndSize',page_info)
      },

      /**/
      handleSizeChange( val) {
        let page_size = val;
        this.page_size = page_size;
        this.currentPage = 1;
        let p = this.currentPage;
        let search = this.inputVal;
        this.delieverPageAndSize();
        console.log(p,page_size);
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("page_size total");
        }
      },
      /**/
      handleCurrentChange(val) {
        let p  = val;
        let page_size = this.page_size;
        let search = this.inputVal;
        console.log(p,page_size);
        this.delieverPageAndSize();
        if(this.otherDate){
          this.get_patients_by_rules(p,page_size,this.dateValue,search);
          console.log("otherdate");
        }else{
          this.get_patients_by_rules(p,page_size,'',search);
          console.log("pagination total");
        }
      },

      turnBlue:function(e){
        let thisActive = e.currentTarget.id;
        if(thisActive == "isActive1"){
          console.log('isActive1');
          this.isActive1 = true;
          this.isActive4 = false;
          this.otherDate = false;
          this.dateValue = '';
          this.refresh();
        }
      },

      get_patients:function (p,size,apply_time,search) {
        let hospital_id = cookie.getCookie('hospital_id');
        axios.get(host+"/patients/?p="+p+"&page_size="+size+"&hospital_id="+1+"&ordering=-apply_time&status=0")
          .then(response=>{
            console.log((response))
            this.tableData = response.data.results
            this.totalNum = response.data.count
          })
          .catch(error=>{
            console.log((error))
          })
      },
      get_patients_by_rules:function (p,size,apply_time,search) {
        let hospital_id = cookie.getCookie('hospital_id');
        axios.get(host+"/patients/?p="+p+"&page_size="+size+
          "&ordering=-apply_time"+"&apply_time="+apply_time+"&hospital_id="+hospital_id+"&search="+search+"&status=0")
          .then(response=>{
            console.log((response))
            this.tableData = response.data.results
            this.totalNum = response.data.count
          })
          .catch(error=>{
            console.log((error))
          })
      },
      selectDate:function () {
        this.isActive1=false;
        this.isActive4=true;
        this.otherDate = true;
        let seperator1 = "-";
        let date = new Date(this.dateValue);
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let strDate = date.getDate();
        if (month >= 1 && month <= 9) {
          month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        let selectdate = year + seperator1 + month + seperator1 + strDate;
        this.dateValue  = selectdate;
        this.currentPage = 1;
        let search = this.inputVal;
        console.log(this.dateValue);
        console.log(search);
        this.get_patients_by_rules(1,this.page_size,selectdate,search);
      },
      searchBtn:function () {
        console.log('searchBtn');
        this.currentPage = 1;
        this.get_patients_by_rules(1,this.page_size,this.dateValue,this.inputVal);
      },
      //
      formatSex: function (row) {
        return row.patient_gender === "1" ? '' : row.patient_gender === "0" ? '' : '';
      },
      //
      formatStatus: function (row) {
        return row.status === "0" ? '' : row.status === "2" ? '' : '';
      },
      //
      formatButton:function(row){
        return row.pic_upload === "1" ? true : row.pic_upload !== "0";
      },
      //
      handleEdit: function (index, row) {
        console.log('qqqqq',index,row);
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
      },
      //
      handleAdd: function () {
        this.addFormVisible = true;
        this.addForm = {
          patient_name: '',
          patient_gender: -1,
          patient_age: 0,
          patient_tel: '',
          address: ''
        };
      },
      //
      checkIsDiagnoseAgain:function(){
        let para = Object.assign({}, this.addForm);
        checkPatient(para).then((res) => {
          console.log(1111,res);
          if(res=="0"){
            console.log("")
          }else{
            this.$alert('', '', {
              confirmButtonText: '',
              callback: action => {
                console.log(res);
                this.addFormVisible = false;
                this.editFormVisible = true;
                this.editForm=res[0].fields;
                this.editForm['patient_id'] = res[0].pk;
                this.editForm['diagnose_again'] = '1';
                this.$refs['addForm'].resetFields();
                // this.$message({
                //   type: 'info',
                //   // message: `action: ${ action }`
                // });
              }
            });

            // this.addFormVisible = false;
            // this.refresh();
          }
        });
      },

      //
      addSubmit: function () {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.addLoading = true;
              //NProgress.start();
              let para = Object.assign({}, this.addForm);
              para['user_token'] = cookie.getCookie('token');
              // para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
              console.log('hhhhh',para); //jsonpara
              addPatient(para).then((res) => {
                this.addLoading = false;
                //NProgress.done();
                this.$message({
                  message: '',
                  type: 'success'
                });
                this.$refs['addForm'].resetFields();
                this.addFormVisible = false;
                this.refresh();
              });
            });
          }
        });
      },
      //
      handleDel: function (index, row) {
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          // this.listLoading = true;
          //NProgress.start();
          let para = { id: row.patient_id };
          removePatient(para).then((res) => {
            // this.listLoading = false;
            //NProgress.done();
            console.log(1111,para);
            if(res.data == '0' ){
              this.$alert('', '', {
                confirmButtonText: '',
                callback: action => {
                    console.log(action)
                }
              });
            }else{
              this.$message({
                message: '',
                type: 'success'
              });
              this.refresh();
            }
          });
        }).catch(() => {

        });
      },

      //
      editSubmit: function () {
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.editLoading = true;
              //NProgress.start();
              let para = Object.assign({}, this.editForm);
              // para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
              editPatientBaicInfo(para).then((res) => {
                this.editLoading = false;
                //NProgress.done();
                this.$message({
                  message: '',
                  type: 'success'
                });
                this.$refs['editForm'].resetFields();
                this.editFormVisible = false;
                this.refresh();
              });
            });
          }
        });
      },

      clickReport:function (index,row) {
        this.editForm = Object.assign({}, row);
        let patient_info = {};
        patient_info['page_size'] = this.page_size;
        patient_info['currentPage'] = this.currentPage;
        patient_info['patient_name'] = this.editForm.patient_name;
        patient_info['patient_gender'] = this.editForm.patient_gender;
        patient_info['patient_age'] = this.editForm.patient_age;
        patient_info['patient_id'] = this.editForm.patient_id;
        patient_info['diagnose_record'] = this.editForm.diagnose_record;
        patient_info['apply_time'] = this.editForm.apply_time;
        this.$store.commit('clickReport',patient_info);
      }
    },
    data() {
      return {
        page_info:{},
        tableData: [],
        dateValue:'',
        isActive1:true,
        isActive4:false,
        currentPage: 1,
        inputVal:'',
        page_size:10,
        totalNum:30,
        otherDate:false,
        pic_upload:false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        editFormVisible: false,//
        editLoading: false,
        editFormRules: {
          patient_name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          patient_tel: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        //
        editForm: {
          patient_id: 0,
          patient_name: '',
          patient_gender: '0',
          patient_age: 0,
          patient_tel:18888888888,
          birth: '',
          address: '',
        },
        addFormVisible: false,//
        addLoading: false,
        addFormRules: {
          patient_name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          patient_tel: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        //
        addForm: {
          patient_id: 0,
          patient_name: '',
          patient_gender: 1,
          patient_age: 0,
          patient_tel: '',
          address: ''
        }

      }
    }
  }

</script>

<style scoped>
.undiagnosed{
  text-align: left;
  margin-top: 20px;
}
  .timezone,.dataZone{
    margin-top: 20px;
  }
  .el-date,.el-icon-date,.icon-shuaxin1{
    color: dodgerblue;
  }

.operationNew{
  margin-top: 20px;
  text-align: center;
  line-height: 41px;
}
  a{
    text-decoration: none;
    color: dodgerblue;
  }

  .el-input__inner{
    border: dodgerblue 1px solid;
    color: dodgerblue;
  }

  .active{
    border: 1px solid dodgerblue;
    border-radius: 4%;
  }

  /*.active>>> a{*/
  /*  color: #FFFFFF;*/
  /*  */
  /*}*/
  /*.active >>> i{*/
  /*  color: #FFFFFF;*/
  /*}*/

  .pagination{
    margin-top: 20px;
    text-align: right;
  }

.el-date-editor.el-input, .el-date-editor.el-input__inner{
  width: 140px;
}

.timepicker >>> i{
  color: dodgerblue;
  font-size: x-large;
}
  .timepicker >>> input {
    border: none;
    font-size: 15px;
  }

  .timepicker >>>input::-webkit-input-placeholder{
    color: dodgerblue;
  }

.timepicker >>>input::-moz-placeholder{
  color: blue;
}

</style>



