<template>
  <el-container>
<el-aside class="aside" id="aside_id">
  <el-row style="text-align: left;padding-top: 20px">
    <i class="icon iconfont icon-icon-test"></i>
  </el-row>
 <el-row style="margin-top: 20px">
   <el-autocomplete
     v-model="state"
     :fetch-suggestions="querySearchAsync"
     placeholder=""
     prefix-icon="el-icon-search"
     @select="handleSelect"
   ></el-autocomplete>
 </el-row>
  <el-row v-bind:style="{backgroundColor:activeColor1}" style="height: 40px;line-height: 40px;text-align: left;margin-top: 20px;">
    
  </el-row>
  <el-row v-for="suggestion in suggestions" v-bind:key="suggestion.id" class="suggestion_section">
    <el-col :span="3">&nbsp;</el-col>
    <el-col :span="18"><a href="javascript:;" @click="addAdvice(suggestion)"><div style="border: 1px solid lightgrey;height: 30px;line-height: 30px;border-radius: 20px">{{suggestion}}</div></a></el-col>
    <el-col :span="3">&nbsp;</el-col>
  </el-row>
  <el-row v-bind:style="{backgroundColor:activeColor2}" style="height: 40px;line-height: 40px;text-align: left;margin-top: 20px;background-color: azure;">
    
  </el-row>

  <el-row v-for="disease in diseases" v-bind:key="disease.id" class="diseases_section">
    <el-col :span="3">&nbsp;</el-col>
    <el-col :span="18"><a href="javascript:;" @click="addDiagnose(disease)"><div style="border: 1px solid lightgrey;height: 30px;line-height: 30px;border-radius: 20px">{{disease}}</div></a></el-col>
    <el-col :span="3">&nbsp;</el-col>
  </el-row>

</el-aside>
  <el-main class="main">
    <el-row><span></span></el-row>

    <el-row style="margin-top: 20px; height: 40px;line-height: 40px;">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16" style="border-top: lightgrey 1px solid;border-bottom:lightgrey 1px solid;text-align: left">
        <el-row>
          <el-col :span="6">:<span>{{patient_info.patient_name}}</span></el-col>
          <el-col :span="6">:<span v-if="patient_info.patient_gender==='0'"></span><span v-else></span></el-col>
          <el-col :span="6">:<span>{{patient_info.patient_age}}</span></el-col>
          <el-col :span="6">:</el-col>
        </el-row>
      </el-col>
      <el-col :span="4" style="border: 1px solid white">&nbsp;</el-col>
    </el-row>

    <el-row style="margin-top: 20px">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">
        <el-row style="text-align: left;font-weight: bold">
          <el-col :span="11">OD</el-col>
          <el-col :span="2">&nbsp;</el-col>
          <el-col :span="11">OS</el-col>
        </el-row>
      </el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="margin-top: 20px">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">
        <el-row style="text-align: left;font-weight: bold">
          <el-col :span="11">
            <div class="demo-image__preview">
              <el-image
                style="height: 300px"
                :src="url1"
                :preview-src-list="srcList">
              </el-image>
            </div>
          </el-col>
          <el-col :span="2">&nbsp;</el-col>
          <el-col :span="11">
            <div class="demo-image__preview">
              <el-image
                style="height: 300px"
                :src="url2"
                :preview-src-list="srcList">
              </el-image>
            </div>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16"><span>:</span></el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;margin-top: 10px;">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">
        <el-row style="border-top: 1px solid lightgray">
          <el-col :span="6"></el-col>
          <el-col :span="6">()</el-col>
          <el-col :span="6"></el-col>
          <el-col :span="6">mm<sup>2</sup></el-col>
        </el-row>
      </el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;" class="dataTable">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">
        <el-row>
          <el-col :span="6"></el-col>
          <el-col :span="6">--</el-col>
          <el-col :span="6">--</el-col>
          <el-col :span="6">--</el-col>
        </el-row>
      </el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;" class="dataTable">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">
        <el-row style="border-bottom: 1px solid lightgray">
          <el-col :span="6"></el-col>
          <el-col :span="6">--</el-col>
          <el-col :span="6">--</el-col>
          <el-col :span="6">--</el-col>
        </el-row>
      </el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;margin-top: 10px">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">:</el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>
    <el-row style="text-align: left;margin-top: 10px">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">:</el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;margin-top: 20px">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16"><span>:</span></el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>
    <el-row style="text-align: left;margin-top: 10px" v-if="diaSeen">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">:
        <el-tag
          :key="tag"
          v-for="tag in dynamicTagsR"
          closable
          :disable-transitions="false"
          @close="handleCloseR(tag)"
          type="info"
          effect="plain">
          {{tag}}
        </el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisibleR"
          v-model="inputValueR"
          ref="saveTagInputR"
          size="small"
          @keyup.enter.native="handleInputConfirmR"
          @blur="handleInputConfirmR"
        >
        </el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInputR">+ </el-button>
      </el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>
    <el-row style="text-align: left;margin-top: 10px" v-if="diaSeen">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">:
        <el-tag
          :key="tag"
          v-for="tag in dynamicTagsL"
          closable
          :disable-transitions="false"
          @close="handleCloseL(tag)"
          effect="plain"
          type="info">
          {{tag}}
        </el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisibleL"
          v-model="inputValueL"
          ref="saveTagInputL"
          size="small"
          @keyup.enter.native="handleInputConfirmL"
          @blur="handleInputConfirmL"
        >
        </el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInputL">+ </el-button>
      </el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;margin-top: 10px" v-if="eyeSeen">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">:{{diaStrR}}</el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;margin-top: 10px" v-if="eyeSeen">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16">:{{diaStrL}}</el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;margin-top: 20px;" v-if="diaSeen">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16"><span>:
       <el-tag
         :key="tag"
         v-for="tag in dynamicTagsA"
         closable
         :disable-transitions="false"
         @close="handleCloseA(tag)"
         type="info"
         effect="plain">
          {{tag}}
        </el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisibleA"
          v-model="inputValueA"
          ref="saveTagInputA"
          size="small"
          @keyup.enter.native="handleInputConfirmA"
          @blur="handleInputConfirmA"
        >
        </el-input>
        <el-button v-if="seen" class="button-new-tag"  size="small" @click="showInputA" >+ </el-button>
        &nbsp;&nbsp;&nbsp;<span v-if="seen2">:</span>{{value2}}
      </span></el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="text-align: left;margin-top: 20px;" v-if="eyeSeen">
      <el-col :span="4">&nbsp;</el-col>
      <el-col :span="16"><span>:{{dynamicTagsA[0]}}

        &nbsp;&nbsp;&nbsp;<span v-if="seen2">:</span>{{value2}}
      </span></el-col>
      <el-col :span="4">&nbsp;</el-col>
    </el-row>

    <el-row style="margin-top: 20px;height: 40px;line-height: 40px" v-if="beforeSeen">
      <el-col :span="4">&nbsp;</el-col>

      <el-col :span="16" style="text-align: left;height: 60px;line-height: 60px;border-top: 1px solid lightgray;
      border-bottom:1px solid lightgray;">
        <el-row>
          <el-col :span="8"><el-button @click="openFullScreen"></el-button></el-col>
          <el-col :span="8"><el-button @click="backToUndiagnosed"></el-button></el-col>
          <el-col :span="8"><el-button></el-button></el-col>
        </el-row>
      </el-col>

      <el-col :span="4"></el-col>
    </el-row>

    <el-row style="margin-top: 20px;height: 40px;line-height: 40px" v-if="finishSeen">
      <el-col :span="4">&nbsp;</el-col>

      <el-col :span="16" style="text-align: left;height: 60px;line-height: 60px;border-top: 1px solid lightgray;
      border-bottom:1px solid lightgray;">
        <el-row>
          <el-col :span="8"><el-button @click="onlyBack"></el-button></el-col>
          <el-col :span="8"><el-button @click="createReport"></el-button></el-col>
          <el-col :span="8">&nbsp;</el-col>
        </el-row>
      </el-col>

      <el-col :span="4"></el-col>
    </el-row>
  </el-main>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%">
      <div class="block">
        <el-date-picker
          v-model="value2"
          align="right"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder=""
          :picker-options="pickerOptions">
        </el-date-picker>
      </div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="cancelSelectDate"> </el-button>
    <el-button type="primary" @click="confirmSelectDate"> </el-button>
  </span>
    </el-dialog>
  </el-container>

</template>

<script>
  import store from '@/vuex/store'
  import axios from 'axios'
  let host = 'http://127.0.0.1:8000/';
  export default {
    data() {
      return {
        patient_info:{},
        restaurants: [],
        state: '',
        timeout:  null,
        suggestions:['','',','],
        diseases:['','','','',
                  '',''],
        fits: ['fill',],
        url1: 'https://www.orbisbiotech.cn/media/xingyuan/2019080410421718855145058_Right_1_origin.png',
        url2: 'https://www.orbisbiotech.cn/media/xingyuan/2019080410421718855145058_Left_2_origin.png',
        srcList: [
          'https://fuss10.elemecdn.com/8/27/f01c15bb73e1ef3793e64e6b7bbccjpeg.jpeg',
          'https://fuss10.elemecdn.com/1/8e/aeffeb4de74e2fde4bd74fc7b4486jpeg.jpeg'
        ],
        dynamicTagsR: [], /**/
        dynamicTagsL: [], /**/
        dynamicTagsA:[],  /**/
        diaStrR:'',       /**/
        diaStrL:'',       /**/
        diaStrA:'',       /**/
        finishSeen:false, /**/
        beforeSeen:true,  /**/
        inputVisibleR: false,
        inputVisibleL: false,
        inputVisibleA: false,
        leftEye:false,
        rightEye:false,
        advice:false,
        inputValueR: '',
        inputValueL: '',
        inputValueA: '',
        seen:true,
        seen2:false,
        eyeSeen:false,
        diaSeen:true,
        activeColor1:'azure',
        activeColor2:'azure',
        dialogVisible: false,
        fullscreenLoading: false,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now();
          },
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
      };
    },
    methods: {
      /**/
      handleCloseR(tag) {
        this.dynamicTagsR.splice(this.dynamicTagsR.indexOf(tag), 1);
      },
      /**/
      showInputR() {
        this.activeColor2 = 'lightgray';
        this.inputVisibleR = true;
        this.rightEye = true;
        this.leftEye = false;
        this.$nextTick(_ => {
          this.$refs.saveTagInputR.$refs.input.focus();
        });
      },
      /**/
      handleInputConfirmR() {

        this.activeColor2 = 'azure';
        let inputValueR = this.inputValueR;
        if (inputValueR) {
          this.dynamicTagsR.push(inputValueR);
        }
        this.inputVisibleR = false;
        this.inputValueR = '';
      },

      /**/
      handleCloseL(tag) {
        this.dynamicTagsL.splice(this.dynamicTagsL.indexOf(tag), 1);
      },
      /**/
      showInputL() {
        this.activeColor2 = 'lightgray';
        this.inputVisibleL = true;
        this.rightEye = false;
        this.leftEye = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputL.$refs.input.focus();
        });
      },
      /**/
      handleInputConfirmL() {
        this.activeColor2 = 'azure';
        let inputValueL = this.inputValueL;
        if (inputValueL) {
          this.dynamicTagsL.push(inputValueL);
        }
        this.inputVisibleL = false;
        this.inputValueL = '';
      },

      /**/
      handleCloseA(tag) {
        this.dynamicTagsA.splice(this.dynamicTagsA.indexOf(tag), 1);
        this.seen = true;
        this.seen2 = false;
        this.value2='';
      },
      /**/
      showInputA() {
        this.leftEye=false;
        this.rightEye=false;
        this.activeColor1 = 'lightgray';
        this.inputVisibleA = true;
        this.advice = true;
        this.$nextTick(_ => {
          this.$refs.saveTagInputA.$refs.input.focus();
        });
      },
      /**/
      handleInputConfirmA() {
        this.activeColor1='azure';
        let inputValueA = this.inputValueA;
        let dynamicTagsA = this.dynamicTagsA;
        if (inputValueA) {
          this.dynamicTagsA.push(inputValueA);
        }
        if(dynamicTagsA.length !== 0 ){
          this.seen = false;
        }
        this.inputVisibleA = false;
        this.inputValueA = '';
      },
      /**/
      addDiagnose(index){
        if(this.leftEye){
          this.dynamicTagsL.push(index);
          let tem_dynamicTagsL = this.duplicateRemove(this.dynamicTagsL);  /**/
          this.dynamicTagsL = tem_dynamicTagsL;
          this.handleInputConfirmL();
        }else if(this.rightEye){
          this.dynamicTagsR.push(index);
          let tem_dynamicTagsR = this.duplicateRemove(this.dynamicTagsR);  /**/
          this.dynamicTagsR = tem_dynamicTagsR;
          this.handleInputConfirmR();
        }
      },
      /**/
      addAdvice(index){
        let dynamicTagsA = this.dynamicTagsA;
        if(this.advice){
          if(dynamicTagsA.length !==0){
            return
          }else{
            this.dynamicTagsA.push(index);
            this.handleInputConfirmA();
          }
          if(index===''){
            this.dialogVisible=true;
            this.seen2=true;
          }
        }
      },

      formatSex: function (row) {
        return row.patient_gender === "1" ? '' : row.patient_gender === "0" ? '' : '';
      },

      /**/
      finishDiagnose(){
        if(this.dynamicTagsA.length===0){
          this.open(); /**/
          return
        }else{
          let diaStrR = this.dynamicTagsR.toString();  /**/
          let diaStrL = this.dynamicTagsL.toString();  /**/
          this.diaStrR = diaStrR;
          this.diaStrL = diaStrL;
          this.submitDiagnose();  /**/
          this.eyeSeen = true;
          this.diaSeen = false;
          this.finishSeen = true;
          this.beforeSeen = false;
        }
      },

      /**/
      openFullScreen() {
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.finishDiagnose();
        }, 500);
      },

      /**/
      backToUndiagnosed:function(){
        let page_info = {};
        page_info['page_size'] = this.patient_info.page_size;
        page_info['currentPage'] = this.patient_info.currentPage;
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.finishDiagnose();
          this.$store.commit('backToUndiagnosed');  /*store.jsbackToUndiagnosed*/
        }, 1000);

      },

      /**/
      onlyBack:function(){
        let page_info = {};
        page_info['page_size'] = this.patient_info.page_size;
        page_info['currentPage'] = this.patient_info.currentPage;
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
          this.$store.commit('backToUndiagnosed',page_info);
        }, 1000);
      },

      /**/
      getConclusion:function () {
        axios.get(host+"/conclusion/?&ordering=-times")
          .then(response=>{
            console.log((response.data));
            let conclusions = response.data;
            let conclusionList = [];
            for(let i=0;i<conclusions.length;i++){
              conclusionList.push(conclusions[i]['conclusion'])
            }
            this.diseases = conclusionList.slice(0,9);
          })
          .catch(error=>{
            console.log((error))
          })
      },

      /**/
      submitDiagnose(){
        let data = {};
        data['patient_id'] = this.patient_info.patient_id;
        data['diagnose_record'] = this.patient_info.diagnose_record;
        data['apply_time'] = this.patient_info.apply_time;
        data['patient_name'] = this.patient_info.patient_name;
        data['patient_gender'] = this.patient_info.patient_gender;
        data['patient_age'] = this.patient_info.patient_age;
        data['left_diagnose'] = this.diaStrL;
        data['right_diagnose'] = this.diaStrR;
        data['diagnose_advice'] = this.dynamicTagsA[0];
        if(this.value2){
          data['review_time'] = this.value2;
        }
        axios.post(`${host}operation/submit/`, data)
          .then(res=>{
            console.log('res=>',res);
          }).catch(error=>{
          console.log((error))
        })
      },

      /**/
      duplicateRemove:function(array){
        let temp1 = []; //
        for(let i = 0; i < array.length; i++){
          if(temp1.indexOf(array[i]) === -1){
            temp1.push(array[i]);
          }
        }
        console.log(temp1);
        return temp1;
      },

      /**/
      createReport:function(){
        // let tem_window = window.open('','','')
        // tem_window.location = '/media/hello.pdf/';
        console.log(1111);
      },

      cancelSelectDate(){
        this.dialogVisible = false;
        this.seen2=false;
        this.value2 = '';
      },

      confirmSelectDate(){
        this.dialogVisible = false;
        if(!this.value2){
          this.seen2=false;
          this.value2 = '';
        }
      },

      open() {
        this.$alert('', '', {
          confirmButtonText: '',
        });
      },

      loadAll() {
        return [
          { "value": "", "address": "1699A13" }
        ];
      },
      querySearchAsync(queryString, cb) {
        let restaurants = this.restaurants;
        let results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          cb(results);
        }, 3000 * Math.random());
      },
      createStateFilter(queryString) {
        return (state) => {
          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect(item) {
        console.log(item);
      },
    },

    mounted() {
      this.restaurants = this.loadAll();
      /*div46px46px*/
      document.getElementById('aside_id').style.height= window.innerHeight - 46 + 'px';
      this.patient_info = this.$store.state.patient_info; /*store.js*/
      this.getConclusion(); /**/
    }
  };
</script>

<style scoped>
.aside{
  width: 20%;
  color: #2F4050;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
}
.main{
  width: 80%;
  height: 100%;
}

a{
  text-decoration: none;
  color: #2F4050;
}
  .suggestion_section,.diseases_section{
    margin-top: 20px;
  }
span{
  font-family: ;
  font-weight: bold;
}

.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 200px;
  margin-left: 10px;
  /*vertical-align: bottom;*/
}
</style>



